<template>
    <div class="anSwiper">
        <swiper :options="swiperOption">
            <swiper-slide v-for="slide in listStays" :key="slide.title">
              <img :src="slide.img" alt="">
              <div class="product-text">
                <p>{{ slide.title }}</p>
                <p></p>
              </div>
              </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
import { listStays } from "~/data/listStays.js";

export default {
  data() {
    return {
      swiperOption: {
        autoplay: 4000,
        setWrapperSize: true,
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true,
        loop: true,
        centeredSlides: true,
        slidesPerView: "1.1",
        spaceBetween: 5
      },
      listStays: listStays
    };
  },
  mounted() {}
};
</script>


<style  lang="scss" scoped>
.anSwiper {
  img {
    width: 100%;
    float: left;
    position: absolute;
    z-index: -1;
  }
  .swiper-slide {
    color: #fff;
    border-radius: 5px;
    overflow: hidden;
    .product-text {
      padding: 5px 10px;
      background-color: rgba(0, 0, 0, 0.7);
      p:nth-child(1) {
        font-size: 20px;
        margin: 5px 0;
      }
      p:nth-child(2) {
        font-size: 13px;
        font-weight: 200;
        padding: 12px 0;
      }
    }
  }
}
</style>



