import Vue from 'vue'
import Router from 'vue-router'
import Hello from '~/components/Hello.vue'
import home from '~/components/home.vue'
import anSwiper from '~/components/anSwiper.vue'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
      name: '',
      component: home
  }, {
      path: '/Hello',
    name: '',
    component: Hello
    }, {
      path: '/anSwiper',
      name: 'swiper',
      component: anSwiper
    }
  ]
})



