<template>
    <div class="homeProduct2">
        <p class="home-product-title"></p>
        <swiper :options="swiperOption">
            <swiper-slide v-for="slide in listStays3" :key="slide.title">
              <img :src="slide.img" alt="">
              <div class="product-text">
                <p>{{ slide.title }}</p>
                <p>{{ slide.icon }}</p>
              </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
import { listStays3 } from "~/data/listStays3.js";

export default {
  data() {
    return {
      swiperOption: {
        loop: false,
        setWrapperSize: true,
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true,
        slidesPerView: "2.2",
        slidesPerColumn: 2,
        spaceBetween: 10
      },
      listStays3: listStays3
    };
  },
  mounted() {}
};
</script>

<style  lang="scss" scoped>
.homeProduct2 {
  padding: 10px;
  img {
    width: 100%;
    position: absolute;
    z-index: -1;
  }
  .swiper-slide {
    background-color: rgba(0, 0, 0, 0.65);
    border-radius: 4px;
    overflow: hidden;
    color: #fff;
    .product-text {
      padding: 5px 10px;
      p:nth-child(1) {
        font-size: 15px;
      }
      p:nth-child(2) {
        font-size: 12px;
        font-weight: 200;
        padding: 3px 0;
      }
    }
  }
}
</style>



