<template>
    <div class="homeProduct2">
        <p class="home-product-title"></p>
        <swiper :options="swiperOption">
            <swiper-slide v-for="slide in listStays2" :key="slide.title">
              <img :src="slide.img" alt="">
              <div class="product-text">
                <p>{{ slide.title }}</p>
              </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
import { listStays2 } from "~/data/listStays2.js";

export default {
  data() {
    return {
      listStays2: listStays2,
      swiperOption: {
        loop: false,
        setWrapperSize: true,
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true,
        // centeredSlides: true,
        slidesPerView: "1.2",
        slidesPerColumn: 3,
        spaceBetween: 10
      },
    };
  },
  mounted() {}
};
</script>

<style  lang="scss" scoped>
.homeProduct2 {
  padding: 10px;
  img {
    width: 60px;
    float: left;
    margin: 10px;
    border-radius: 5px;
  }
  .swiper-slide {
    border-radius: 4px;
    background-color: #fff;
    .product-text {
      padding: 10px;
    }
  }
}
</style>



