<template>
  <div id="app">
    <header>
      <img src="http://ooi407n8x.bkt.clouddn.com/an.jpg" alt="">
      <el-button></el-button>
    </header>
    <transition name="fade" mode="out-in">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "Want something new?"
    };
  },
  methods: {}
};
</script>

<style lang="scss">
#app {
  height: 100%;
}

.fade-enter-active,
.fade-leave-active {
  transition: all 0.4s ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

header {
  padding: 5px;
  margin-bottom: 5px;
  img {
    width: 40px;
    border-radius: 50%;
  }
  button {
    float: right;
    height: 40px;
    font-size: 17px;
  }
}
</style>



